//login頁面
@media (max-width: 767px) {
    .phone-col {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
    /* 調整連結社群帳號區塊 */
    .phone-layout {
        position: static !important;
    }
    .phone-layout {
        padding: 0 !important;
    }
    .phone-layout .col {
        border-right: 2px solid var(--light);
    }
}

.login-panel * {
    border: none;
}

.login-form .input-container {
    background-color: var(--light);
    display: flex;
    align-items: center;
    padding: 16px 20px;
}

.login-form input:focus {
    border: none !important;
    box-shadow: none !important;
}

.id-platform li {
    cursor: pointer;
}

.id-platform li img {
    height: 20px;
}

.id-platform li img[src="images/ic-google.svg"] {
    height: 25px;
}

@media (max-width: 767px) {
    .id-platform li {
        max-height: 56px;
    }
}

// 購物明細 


.cart-content .item-img {
    min-width: 110px;
    height: 110px;
}

.cart-content .pagination li {
    max-width: 48px;
    max-height: 48px;
}

.cart-content .btn-group .btn,
.cart-content input[type="text"] {
    max-width: 48px !important;
    max-height: 48px !important;
}

.cart-content .remove-btn {
    cursor: pointer;
}

.cart-content .remove-btn:focus,
.cart-content .remove-btn:hover {
    color: var(--danger) !important;
}


.address option {
    flex: 1;
}

.purchase-list li {
    height: 80px;
}

// 發票
.receipt-page .receipt-type p{
    line-height: 2;
}
.receipt-page .receipt-type .active{
    color: var(--primary) !important;
    background :var(--light);
}

// 付款成功

.paid-page .linked-circles li{
    width: 24px;
    height: 24px;
}
.paid-page .linked-circles::before{
    background-color: var(--primary);
}


